import Step from "../../../base/step/step.vue"
import Scroll from '../../../base/scroll/scroll.vue'
import Upload from '../../../base/upload/upload.vue'

export default {
  name: "taskFiveStep2",
  components: {
    Step,
    Scroll,
    Upload
  },
  data() {
    return {
      isOk: true, //
      favImg: [], //
    }
  },
  methods: {
    doNext() {
      if (this.favImg.length === 0) {
        this.$vux.alert.show({
          title: '',
          content: '',
        });
        return false
      }
      let that = this;
      this.$axios.post('/api/orderOperate/seventhOrderSubmit', {
        buyerTaskRecordId: that.$route.query.buyerTaskRecordId,
        favImg: this.favImg
      }).then((res) => {
        console.log(res)
        if (res.data.code === "200") {
          this.$router.push({ name: 'submitSuccess', query: { type: 'task5' } })
        }
      }).catch((error) => {
        this.$vux.alert.show({
          title: '',
          content: '',
        })
      })
    }
  }
}



